import axios from 'axios'
import Vue from 'vue'
import Message from '@/utils/message'
// import store from '../store'
import { getToken } from '@/utils/auth'

// axios
const service = axios.create({
  baseURL: process.env.BASE_API, // apibase_url
  timeout: 15000 // 
})

// request
service.interceptors.request.use(config => {
	// header
  if (getToken()) {
    // config.headers['session'] = getToken() // token 
  } else {
    console.log('goto miniProgram')
    Vue.wechat.miniProgram.navigateTo({
      url: '/pages/activities/activityWebView?type=newgift'
    })
  }
  return config
}, error => {
  // Do something with request error
  console.log(error) // for debug
  Promise.reject(error)
})

// respone
service.interceptors.response.use(
  response => {
  /**
  * code20000 
  */
    const res = response.data
    // if (res.code !== 0) {
    const isDownload = response.headers['content-type'] === 'application/octet-stream;charset=UTF-8'
    // const isDownload = 'application/octet-stream'.indexOf(response.headers['content-type']) !== -1
    if (res.code !== 0 && !isDownload && res.errorcode!=0) {
			Message.toast(res.errormsg || res.data || 'code')
      // 
      const errList = [
        { code: 1013, msg: 'IP', relogin: true },
        { code: 10002, msg: '', relogin: true },
        { code: 1015, msg: '!', relogin: true },
        { code: 1001, msg: '' },
        { code: 1000, msg: '' },
        { code: 500, msg: '' },
        { code: 1012, msg: '', relogin: true }
      ]
      // 50008:token; 50012:;  50014:Token ;
      for (const error of errList) {
        if (res.errorcode === error.code) {
          if (error.relogin) { // 
            // MessageBox.confirm('', '', {
            //   confirmButtonText: '',
            //   cancelButtonText: '',
            //   type: 'warning'
            // }).then(() => {
            //   store.dispatch('FedLogOut').then(() => {
            //     location.reload()// vue-router bug
            //   })
            // })
          } else {
						Message.toast(res.errormsg || error.msg)
          }
        }
      }
      // return Promise.reject('error')
      return Promise.reject(res)
    } else {
      return response.data
    }
  },
  error => {
		console.log('err' + error)// for debug
		Message.toast(error.message || error.msg)
    return Promise.reject(error)
  }
)

export default service



